import * as THREE from 'three'
import output_fragment from './shader/output_fragment.glsl.js';

class FlyLine {
    R = 0;

    constructor(R) {
        this.R = R
    }

    createArcLine(startSphere, endSphere) {
        const arcLine = this.flyArc(startSphere, endSphere)
        const startAngle = arcLine.startAngle
        const endAngle = arcLine.endAngle
        const flyAngle = (endAngle - startAngle) / 2
        const flyR = Math.abs(arcLine.topCoord.y - arcLine.center.y)
        const flyLine = this.createFlyLine(flyR, startAngle, startAngle + flyAngle)
        flyLine.position.y = arcLine.center.y
        flyLine.flyAngle = flyAngle
        flyLine.startAngle = startAngle
        flyLine.flyEndAngle = endAngle - startAngle - flyAngle
        arcLine.add(flyLine)
        return {
            arcLine,
            flyLine
        }
    }

    /**
     * @description: 
     * 
     * @param {number} flyR 
     * @param {number} startAngle 
     * @param {number} endAngle 
     * 
     * @return {THREE.Line} arcLine
     * */
    createFlyLine(flyR, startAngle, endAngle) {
        const arc = new THREE.ArcCurve(0, 0, flyR, startAngle, endAngle)
        const pointArr = arc.getSpacedPoints(100)
        const geometry = new THREE.BufferGeometry().setFromPoints(pointArr)
        const percentArr = []
        const center = Math.floor(pointArr.length / 2)
        for (let i = 0; i < pointArr.length; i++) {
            if (i <= center) {
                percentArr.push((i / center) * (endAngle - startAngle) / (2 * Math.PI) * 5) 
            } else {
                percentArr.push((1 - (i - center) / center) * (endAngle - startAngle) / (2 * Math.PI) * 5)
            }
        }
        geometry.attributes.percent = new THREE.BufferAttribute(new Float32Array(percentArr), 1)
        const colorArr = []
        const color1 = new THREE.Color(0x009999)
        const color2 = new THREE.Color(0xffffff)
        for (let i = 0; i < pointArr.length; i++) {
            let color = color2
            /* if (i <= center) {
                color = color1.clone().lerp(color2.clone(), i / center)
            } else {
                color = color1.clone().lerp(color2.clone(), 1 - (i - center) / center)
            } */
            colorArr.push(color.r, color.g, color.b)
        }
        geometry.attributes.color = new THREE.BufferAttribute(new Float32Array(colorArr), 3)
        const material = new THREE.PointsMaterial({
            size: 4.0, //
            depthWrite: false,
            transparent: true,
            vertexColors: true
        })
        material.onBeforeCompile = function (shader) {
            shader.vertexShader = shader.vertexShader.replace(
                'void main() {',
                [
                    'attribute float percent;',
                    'void main() {'
                ].join('\n')
            )
            shader.vertexShader = shader.vertexShader.replace(
                'gl_PointSize = size;',
                [
                    'gl_PointSize = size * percent;'
                ].join('\n')
            )
            shader.fragmentShader = shader.fragmentShader.replace('#include <output_fragment>', output_fragment)
        }
        const points = new THREE.Points(geometry, material)
        return points
    }

    flyArc(startSphereCoord, endShpereCoord) {
        const startEndQua = this._3Dto2D(startSphereCoord, endShpereCoord)
        const arcLine = this.arcXOY(startEndQua.startPoint, startEndQua.endPoint)
        arcLine.quaternion.multiply(startEndQua.quaternion)
        return arcLine
    }

    /**
     * @param {Vector3} startPoint 
     * @param {Vector3} endPoint 
     * */
    arcXOY(startPoint, endPoint) {
        // 1. 
        const middleV3 = new THREE.Vector3().addVectors(startPoint, endPoint).multiplyScalar(0.5)
        // 2. 
        const dir = middleV3.clone().normalize()
        // 3.   
        // 120 
        // earthRadianAngle
        let earthRadianAngle = this.radianAOB(startPoint, endPoint, new THREE.Vector3(0, 0, 0))
        earthRadianAngle = earthRadianAngle > Math.PI / 3 ? Math.PI / 3 : earthRadianAngle
        // 4. ()
        const arcTopCoord = dir.multiplyScalar(this.R + earthRadianAngle * 10) // this.R + earthRadianAngle * this.R * 0.1
        // 5. 
        const flyArcCenter = this.threePointCenter(startPoint, endPoint, arcTopCoord)
        // 6. 
        const flyArcR = Math.abs(flyArcCenter.y - arcTopCoord.y)
        // 7. y
        const flyRadianAngle = this.radianAOB(startPoint, new THREE.Vector3(0, -1, 0), flyArcCenter)
        // 8. 
        const startAngle = -Math.PI / 2 + flyRadianAngle
        // 9. 
        const endAngle = Math.PI - startAngle
        // 10.
        const arcLine = this.circleLine(flyArcCenter.x, flyArcCenter.y, flyArcR, startAngle, endAngle)
        arcLine.center = flyArcCenter
        arcLine.topCoord = arcTopCoord
        arcLine.startAngle = startAngle
        arcLine.endAngle = endAngle
        return arcLine
    }

    /**
     * 
     * @param {Vector3} startSphere  
     * @param {Vector3} endShpere 
     */
    _3Dto2D(startSphere, endShpere) {
        // 1. 
        const origin = new THREE.Vector3(0, 0, 0)
        // 2. 
        const startDir = startSphere.clone().sub(origin).normalize()
        // 3. 
        const endDir = endShpere.clone().sub(origin).normalize()
        // 4. ()
        const normal = startDir.clone().cross(endDir).normalize()
        const xoyNormal = new THREE.Vector3(0, 0, 1)
        // 5. xoy
        const quaternion_XOY = new THREE.Quaternion().setFromUnitVectors(normal, xoyNormal)
        // 6. 
        const startSphere_XOY = startSphere.clone().applyQuaternion(quaternion_XOY)
        const endSphere_XOY = endShpere.clone().applyQuaternion(quaternion_XOY)
        // 7. 
        const centerQuaternion_XOY = new THREE.Vector3().addVectors(startSphere_XOY, endSphere_XOY).multiplyScalar(0.5)
        // 8. 
        const centerQuaternionDir = centerQuaternion_XOY.clone().sub(origin).normalize()
        // 9. xoyy
        const quaternion_XOY_Y = new THREE.Quaternion().setFromUnitVectors(centerQuaternionDir, new THREE.Vector3(0, 1, 0))
        // 10.XOYy
        const startSphere_XOY_Y = startSphere_XOY.clone().applyQuaternion(quaternion_XOY_Y)
        const endSphere_XOY_Y = endSphere_XOY.clone().applyQuaternion(quaternion_XOY_Y)
        // 11.
        const quaternionIntert = quaternion_XOY.clone().invert().multiply(quaternion_XOY_Y.clone().invert())
        return {
            quaternion: quaternionIntert,
            startPoint: startSphere_XOY_Y,
            endPoint: endSphere_XOY_Y
        }
    }

    /**
     * @description 
     * 
     * @param x 
     * @param y 
     * @param r 
     * @param startAngle 
     * @param endAngle 
     * 
     * @return 
     * */
    circleLine(x, y, r, startAngle, endAngle) {
        const geometry = new THREE.BufferGeometry()
        const arcCurve = new THREE.ArcCurve(x, y, r, startAngle, endAngle, false)
        const points = arcCurve.getSpacedPoints(50)
        geometry.setFromPoints(points)
        const material = new THREE.LineBasicMaterial({
            color: 0x00ffff,
            transparent: true,
            opacity: 0
        })
        const line = new THREE.Line(geometry, material)
        return line
    }


    radianAOB(A, B, O) {
        // 
        const dir1 = A.clone().sub(O).normalize()
        // 
        const dir2 = B.clone().sub(O).normalize()
        // 
        const cosAngle = dir1.clone().dot(dir2)
        // 
        const radianAngle = Math.acos(cosAngle)
        return radianAngle
    }

    /**
     * @description 
     * @param {Vector3} p1 1
     * @param {Vector3} p2 2
     * @param {Vector3} p3 3
     * */
    threePointCenter(p1, p2, p3) {
        var L1 = p1.lengthSq();//p1
        var L2 = p2.lengthSq();
        var L3 = p3.lengthSq();
        var x1 = p1.x, y1 = p1.y, x2 = p2.x, y2 = p2.y, x3 = p3.x, y3 = p3.y;
        var S = x1 * y2 + x2 * y3 + x3 * y1 - x1 * y3 - x2 * y1 - x3 * y2;
        var x = (L2 * y3 + L1 * y2 + L3 * y1 - L2 * y1 - L3 * y2 - L1 * y3) / S / 2;
        var y = (L3 * x2 + L2 * x1 + L1 * x3 - L1 * x2 - L2 * x3 - L3 * x1) / S / 2;
        // 
        var center = new THREE.Vector3(x, y, 0);
        return center
    }
}

export default FlyLine